@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,400;0,700;1,400&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.App {
  background: black url(/test/static/media/tenor.3c891083.gif) no-repeat center fixed;
  /*background: black;*/
  background-size: 90%;
  color: white;
}

html {
  scroll-behavior: smooth;
  font-family: "Roboto Mono", monospace;
  scrollbar-width: none;
}

html::-webkit-scrollbar {
  display: none;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  width: 100%;
  z-index: 100;
  height: 50px;
  background: rgb(33, 33, 33);
}

.header a {
  text-decoration: none;
  position: relative;
  display: inline-block;
  color: white;
  overflow: hidden;
  background: linear-gradient(to right, red, red 50%, white 50%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 100%;
  background-position: 100%;
  transition: background-position 275ms ease;
}

.header a:hover {
  background-position: 0 100%;
}

#intro {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#intro .greeting {
  font-size: 4em;
  margin: 20px;
}

#intro .name {
  color: red;
  font-weight: bold;
}

#intro .description {
  font-size: 2em;
}

.link {
  height: 50px;
  padding: 20px;
}

.link:hover {
  opacity: 0.5;
}

#projects {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
