}

.justify-right .screenshot {
  border-radius: 10px 0 0 10px;
}

.justify-left .screenshot {
  border-radius: 0 10px 10px 0;
}

.project .tech {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.project .tech-icon {
  height: 50px;
